<template lang="pug">
  #skill
    v-container.text-xs-center(justfy-center grid-list-md)
      v-layout(row wrap)
        v-flex(xs12)
          .display-1.font-weight-regular HABILIDADES
            v-icon.ml-2(size="50") star
      v-layout(row wrap)
        v-flex(
          xs6
          md3
          v-for="item in skills"
          :key="item.text"
          )
          .text-xs-center
            img(:src="item.image" height="80px")
            v-spacer
            span.title(:class="item.color_text") {{ item.text }}
            v-spacer
            v-chip.body-2(
              :color="item.color"
              :class="item.color_level"
              ) {{ item.level | level }}
</template>

<script>
import SkillData from '@/assets/SkillData';
import { level } from '@/filters/LevelFilter';

export default {
  filters: {
    level,
  },
  data: () => ({
    skills: SkillData,
  }),
};
</script>



